/* Color Palette
theme 1(space cadet): #1C2541
theme 2(ruby): #A4161A
theme text 1(cultured): #F5F3F4
*/

:root {
  --bg-theme: #161a1d;
  --accent-theme: #3a506b;
  --text-theme: #f5f3f4;
}

.bg-theme-1 {
  background-color: var(--bg-theme);
}

.bg-theme-2 {
  background-color: var(--accent-theme);
}

.text-theme-1 {
  color: var(--text-theme);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Andale Mono";
  font-weight: 500;
  margin: 0.25em;
  line-height: 1.25;
}

h1 {
  font-size: 2em;
}
h2 {
  font-size: 1.3em;
}

.title-bar {
  width: 25em;
}

.subtitle-bar {
  width: 200px;
  margin-left: 9em;
}

.navbar {
  margin-right: 2em;
  border-top: solid 2px var(--accent-theme);
  border-bottom: solid 2px var(--accent-theme);
  color: var(--text-theme);
}

.navbar-light .navbar-nav .nav-link.active {
  color: #6fffe9;
  text-shadow: 0 0 15px #b3f0e7;
}

.navbar-light .navbar-nav .nav-link {
  font-family: "Andale Mono";
  color: var(--text-theme);
}

.navbar-light .navbar-nav .nav-link:hover {
  color: #6fffe9;
}
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 1em;
  letter-spacing: 0.1em;
  background: var(--bg-theme);
  color: var(--text-theme);
  font-family: "Courier New";
}

.bio-image {
  height: 150px;
  width: 150px;
  background-size: cover;
  background-image: url("./assets/images/IMG_5050.jpeg");
  background-position: center;
  border: solid 2px var(--accent-theme);
  margin: 0 auto;
}

.section-heading {
  background-color: var(--text-theme);
  color: var(--accent-theme);
  margin-left: 20px;
  width: 12em;
  height: 4em;
}

footer {
  width: 100%;
  border-top: solid 4px var(--accent-theme);
  padding-top: 1.75em;
}

.contact-link {
  font-size: 2.2em;
}

.footer-links {
  width: 15rem;
}

footer p {
  vertical-align: middle;
  font-size: 0.75em;
}

.footer-logo {
  font-size: 3em;
}

.icon {
  position: relative;
  top: 8px;
}

footer p span {
  font-size: 1.75em;
}

footer a:hover {
  text-decoration: none;
}

.card {
  background-color: var(--text-theme);
  border: solid 4px var(--text-theme);
  color: var(--accent-theme);
  text-align: center;
}

.project {
  display: block;
  max-width: 100%;
  height: auto;
}

.project-title {
  text-align: center;
  font-family: "Andale Mono";
  font-weight: 900;
  font-size: 1.5em;
  margin-top: 0.5em;
}

.site-link {
  background-color: var(--accent-theme);
  color: var(--text-theme);
  font-size: 1.5em;
  padding: 0.2em 1.2em;
  border-radius: 0px;
}

.site-link:hover {
  background-color: #6fffe9;
}

.icon-link {
  font-size: 3em;
  color: var(--accent-theme);
}

.icon-link:hover {
  color: #b3f0e7;
}

.screenshot {
  height: 300px;
  width: 400px;
  margin: 10px 6px;
}

.resume-text {
  margin: 0 1em 1em 1em;
}
.resume-link-container {
  border-top: solid 10px var(--text-theme);
}
.resume-link {
  background-color: #6fffe9;
  width: 10em;
  margin-right: 3em;
  margin-top: 1.5em;
}
.resume-link-text {
  color: var(--bg-theme);
  margin: 0.5em 0;
}
.skills-border {
  border-top: solid 10px var(--text-theme);
  border-bottom: solid 10px var(--text-theme);
}

.skills-header {
  width: 12em;
  text-align: center;
}

@media screen and (min-width: 1025px) {
  header {
    margin-right: 0;
  }

  .title-bar {
    align-self: flex-start;
    margin-left: 100px;
  }

  .subtitle-bar {
    align-self: flex-start;
    margin-left: 90px;
  }

  .navbar {
    margin-right: 5em;
  }
  .navbar-light .navbar-nav .nav-link {
    margin: 0 1em;
  }

  .bio-image {
    height: 150px;
    width: 150px;
  }

  .bio-text {
    width: 80%;
    margin: 0 auto;
  }

  .contact-text {
    width: 80%;
    margin: 0 auto;
  }

  .contact-form {
    width: 50%;
    margin: 0 auto;
  }

  .resume-text {
    margin: 0 auto;
    width: 80%;
  }

  .resume-link {
    margin-right: 320px;
  }
}
